import { State } from "../../types";

export interface ILoadPersistedStateAction {
  corrections: State["textCorrections"] | null;
  dataPoints: State["dataPoints"] | null;
  type: "LOAD PERSISTED STATE";
}

export const loadPersistedState = (state: State, action: ILoadPersistedStateAction): State => {
  let dataPoints = state.dataPoints;
  if (action.dataPoints !== null) {
    dataPoints = action.dataPoints;
  }
  let textCorrections = state.textCorrections;
  if (action.corrections !== null) {
    textCorrections = action.corrections;
  }
  return {
    ...state,
    dataPoints,
    textCorrections,
  };
};
