import cx from "classnames";
import React, { memo, useRef, useState } from "react";

import { Cell, ContentType, Page as PageType, State, TableContent } from "../../types";

type Props = Readonly<{
  activeBlockId: string | null;
  content: State["content"];
  onSelectBlock: (id: string, text: string) => void;
  page: PageType;
}>;

const NULL_CELL = {
  bottom: 0,
  left: 0,
  right: 0,
  top: 0,
};

export const Page = memo(({ activeBlockId, content, onSelectBlock, page }: Props) => {
  const pageRef = useRef<HTMLDivElement>(null);
  const [imageLoaded, setImageLoaded] = useState(false);
  let contents = null;
  let height = 0;
  if (pageRef.current && imageLoaded) {
    const scale = pageRef.current.clientWidth / page.width;
    height = page.height * scale;
    contents = page.blocks.map(b => {
      const zoneId = `zone-${b.contentId}`;
      const style = {
        height: b.height * scale,
        left: b.x * scale,
        top: b.y * scale,
        width: b.width * scale,
      };
      const selected = !!activeBlockId && b.contentId === activeBlockId;
      const parentContent = content[b.contentId];
      if (!parentContent) {
        return null;
      }
      switch (b.type) {
        case ContentType.Table: {
          const tableContent = parentContent as TableContent;
          const cells: Array<Cell> = b.rows.reduce(
            (prev, next) => prev.concat(next.cells),
            [] as Array<Cell>,
          );
          return (
            <div
              className={cx("zone", { selected })}
              key={zoneId}
              id={zoneId}
              onClick={e => {
                e.preventDefault();
                onSelectBlock(b.contentId, tableContent.text);
              }}
              style={style}
            >
              {cells.map(cell => {
                const cellSelected = !!activeBlockId && activeBlockId === cell.id;
                if (isNaN(cell.height) || isNaN(cell.width) || isNaN(cell.x) || isNaN(cell.y)) {
                  return (
                    <div
                      className={cx("zone", "zone-cell", { selected: cellSelected })}
                      key={cell.id}
                      id={cell.id}
                      onClick={e => {
                        e.preventDefault();
                      }}
                      style={NULL_CELL}
                    />
                  );
                }
                return (
                  <div
                    className={cx("zone", "zone-cell", { selected: cellSelected })}
                    key={cell.id}
                    id={cell.id}
                    onClick={e => {
                      e.preventDefault();
                      onSelectBlock(cell.id, cell.element.textContent || "");
                    }}
                    style={{
                      height: cell.height * scale,
                      left: cell.x * scale,
                      top: cell.y * scale,
                      width: cell.width * scale,
                    }}
                  />
                );
              })}
            </div>
          );
        }
        default:
          return (
            <div
              className={cx("zone", { selected })}
              key={zoneId}
              id={zoneId}
              onClick={e => {
                e.preventDefault();
                onSelectBlock(
                  b.contentId,
                  parentContent.type === ContentType.Text ? parentContent.text : "",
                );
              }}
              style={style}
            />
          );
      }
    });
  }
  return (
    <div className="page-wrapper">
      <div className="page" style={{ height: `${height}px` }} ref={pageRef}>
        <img
          className="position-absolute"
          onLoad={() => {
            setImageLoaded(true);
          }}
          src={page.backgroundSrc}
        />
        {contents}
      </div>
    </div>
  );
});
Page.displayName = "Page";
